:root {
  --primary-color: #fff100;
  --secondary-color: #542e90;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
}

.container {
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  img {
    width: 100%;
    height: 100%;
  }
}
